import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { ChallengeError } from '../../util';

@Component({
  selector: 'bb-transaction-signing-error',
  templateUrl: 'error.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionSigningErrorComponent {
  @Output() closeModal = new EventEmitter<Error>();
  @Input() error: ChallengeError | undefined;

  onClose() {
    this.closeModal.emit(new Error(this.error && (this.error.errorDescription || this.error.errorCode)));
  }
}
